  body #root {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(64, 81, 83, 0.295)), to(rgba(248, 248, 248, 0.342)));
    background: -webkit-linear-gradient(rgba(64, 81, 83, 0.295), rgba(248, 248, 248, 0.342));
    background: linear-gradient(rgba(64, 81, 83, 0.295), rgba(248, 248, 248, 0.342)); }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40px;
  pointer-events: none; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

